          <v-col cols="12">
            <v-card>
              <v-card-title>
                <v-row>
                  <v-col cols="10">
                    {{ $t("main_content") }}
                  </v-col>
                </v-row>
              </v-card-title>
              <v-card-text>
                <v-row>
                  <v-col cols="4">
                    <v-file-input
                      v-model="content.image"
                      :label="$t('image')"
                      hide-details
                      v-if="!content.image"
                    ></v-file-input>
                    <v-text-field
                      clearable
                      v-model="content.image"
                      v-if="content.image"
                    ></v-text-field>
                    <v-img
                      v-if="content.image"
                      :src="get_url(content.image)"
                      width="300"
                    >
                    </v-img>
                  </v-col>
                  <v-col cols="8">
                    <v-text-field
                      :label="$t('title')"
                      v-model="content.title"
                    ></v-text-field>
                    <v-textarea
                      v-model="content.text"
                      :label="$t('text')"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-btn dark color="primary" block @click="saveContent">
              <v-icon> mdi-check </v-icon>
              {{ $t("save") }}
            </v-btn>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-card-title>
                <v-row>
                  <v-col cols="10">
                    {{ $t("counters") }}
                  </v-col>
                  <v-col>
                    <v-btn icon @click="counters.push({})">
                      <v-icon> fas fa-plus </v-icon>
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card-title>
              <v-card-text>
                <v-row>
                  <v-col cols="4" v-for="(counter, i) in counters">
                    <v-row>
                      <v-col cols="10">
                        <v-text-field
                          :label="$t('title')"
                          v-model="counter.title"
                        >
                        </v-text-field>
                        <v-text-field
                          :label="$t('value')"
                          v-model="counter.value"
                        >
                        </v-text-field>
                      </v-col>
                      <v-col cols="2">
                        <v-btn icon @click="counters.splice(i, 1)">
                          <v-icon> fas fa-plus </v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-btn dark color="primary" block @click="saveCounters">
              <v-icon> mdi-check </v-icon>
              {{ $t("save") }}
            </v-btn>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-card-title>
                <v-row>
                  <v-col cols="10">
                    {{ $t("paragraph") }}
                  </v-col>
                  <v-col>
                    <v-btn icon @click="paragraph.menus.push({})">
                      <v-icon> fas fa-plus </v-icon>
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card-title>
              <v-card-text>
                <v-row>
                  <v-col cols="4">
                    <v-file-input
                      v-model="paragraph.image"
                      :label="$t('image')"
                      hide-details
                      v-if="!paragraph.image"
                    ></v-file-input>
                    <v-img
                      v-if="paragraph.image"
                      :src="get_url(paragraph.image)"
                      width="300"
                    >
                    </v-img>
                  </v-col>
                  <v-col cols="8">
                    <v-row>
                      <v-col cols="4" v-for="p in paragraph.menus">
                        <v-text-field :label="$t('title')" v-model="p.title">
                        </v-text-field>
                        <v-textarea :label="$t('content')" v-model="p.text">
                        </v-textarea>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-btn dark color="primary" block @click="saveParagraph">
              <v-icon> mdi-check </v-icon>
              {{ $t("save") }}
            </v-btn>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-card-title>
                <v-row>
                  <v-col cols="10">
                    {{ $t("team") }}
                  </v-col>
                  <v-col cols="2">
                    <v-btn icon @click="team.push({})">
                      <v-icon> fas fa-plus </v-icon>
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card-title>
              <v-card-text>
                <v-row v-for="(item, i) in team">
                  <v-col cols="4">
                    <v-file-input
                      v-model="item.image"
                      :label="$t('image')"
                      hide-details
                      v-if="!item.image"
                    ></v-file-input>
                    <v-img
                      v-if="item.image"
                      :src="get_url(item.image)"
                      width="300"
                    >
                    </v-img>
                  </v-col>
                  <v-col cols="4">
                    <v-text-field v-model="item.name" :label="$t('name')">
                    </v-text-field>
                    <v-text-field v-model="item.title" :label="$t('title')" />
                    <v-row>
                      <v-col cols="3">
                        <v-text-field
                          prepend-inner-icon="fab fa-facebook"
                          v-model="item.facebook"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="3">
                        <v-text-field
                          prepend-inner-icon="fab fa-instagram"
                          v-model="item.instagram"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="3">
                        <v-text-field
                          prepend-inner-icon="fab fa-twitter"
                          v-model="item.twitter"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>
                  <v-col cols="4">
                    <v-btn icon @click="slider.push({})">
                      <v-icon> fas fa-plus </v-icon>
                    </v-btn>
                    <v-btn icon @click="slider.splice(i, 1)">
                      <v-icon> fas fa-times </v-icon>
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-btn dark color="primary" block @click="saveTeam">
              <v-icon> mdi-check </v-icon>
              {{ $t("save") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
    </base-material-card>
  </v-container>
</template>
<script>
import { mapActions, mapState } from "vuex";
export default {
  props: {
    item: {
      default: () => {
        return {};
      },
    },
  },
  data() {
    return {
      slider: [],
      featured_items: [],
      featured_posts: [],
      clients: [],
      partners: [],
      content: {},
      counters: [],
      paragraph: {
        image: null,
        menus: [],
      },
      team: [],
    };
  },
  mounted() {
    this.$store.dispatch("item/index");
    this.$store.dispatch("setting/index");
  },
  methods: {
    ...mapActions("accessor", ["store"]),
    get_url(image) {
      return typeof image != "string" ? URL.createObjectURL(image) : image;
    },
    handleFileUpload(e) {
      console.log(e);
      let files = e.target.files;
      for (var i = 0; i < files.length; i++) {
        this.images.push(files[i]);
      }
      // for( let file in e.target.files)
      // this.images.concat(e.target.files);
    },
    async image_upload(file) {
      if (typeof file == "string")
        return {
          url: file,
        };
      return await this.$store.dispatch("media/store", {
        file,
        is_file: true,
        gallery_id: 1,
      });
    },
    async saveTeam() {
      for (let i in this.team) {
        let image = await this.image_upload(this.team[i].image);
        this.team[i].image = image.url;
      }
      this.$store.dispatch("setting/store", {
        key: "team",
        value: JSON.stringify(this.team),
      });
    },
    async saveParagraph() {
      let image = await this.image_upload(this.paragraph.image);
      this.paragraph.image = image.url;
      this.$store.dispatch("setting/store", {
        key: "paragraph",
        value: JSON.stringify(this.paragraph),
      });
    },
    async saveContent() {
      let image = await this.image_upload(this.content.image);
      this.content.image = image.url;
      this.$store.dispatch("setting/store", {
        key: "content",
        value: JSON.stringify(this.content),
      });
    },
    async saveCounters() {
      this.$store.dispatch("setting/store", {
        key: "counters",
        value: JSON.stringify(this.counters),
      });
    },
  },
  computed: {
    ...mapState({
      items: (state) => state.item.all,
      posts: (state) => state.post.all,
      settings: (state) => state.setting.all,
    }),
  },
  watch: {
    settings: {
      handler() {
        if (this.settings) {
          this.content = this.settings.content || {};
          this.paragraph = this.settings.paragraph || { menus: [] };
          this.counters = this.settings.counters || [];
          this.team = this.settings.team || [];
        }
      },
      deep: true,
    },
  },
};
</script>