<template>
  <v-container id="regular-tables" fluid tag="section">
    <base-v-component heading="items_form" />

    <base-material-card
      icon="mdi-clipboard-text"
      title="Item Form"
      class="px-5 py-3"
    >
      <v-form>
        <v-row>
          <v-col cols="3">
            <v-text-field
              v-for="(translation, index) in item.translations.filter(
                (i) => i.field == 'name'
              )"
              v-model="translation.value"
              :label="$t('name')"
            >
              <template v-slot:prepend>
                <v-autocomplete
                  dense
                  v-model="translation.locale"
                  :items="locales"
                  item-text="locale.native_name"
                  item-value="locale.iso_639"
                >
                </v-autocomplete>
              </template>
              <template v-slot:append-outer>
                <v-btn
                  v-if="index > 0"
                  icon
                  @click="
                    item.translations.splice(
                      item.translations.findIndex(
                        (i) =>
                          i.field == 'name' && i.locale == translation.locale
                      ),
                      1
                    )
                  "
                >
                  <v-icon> fas fa-times </v-icon>
                </v-btn>
                <v-btn @click="item.translations.push({ field: 'name' })" icon>
                  <v-icon> fas fa-plus </v-icon>
                </v-btn>
              </template>
            </v-text-field>
          </v-col>
          <!-- <v-col cols="3">
            <v-autocomplete
              dense
              v-model="item.parent_id"
              :items="items"
              item-text="name"
              item-value="id"
              :label="$t('accessor_for')"
            >
            </v-autocomplete>
          </v-col> -->
          <v-col cols="3">
            <v-text-field v-model="item.code" :label="$t('code')" />
          </v-col>
          <v-col cols="3">
            <v-text-field v-model="item.price" :label="$t('price')" />
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="3">
            <v-autocomplete
              :items="types"
              item-text="name"
              item-value="id"
              v-model="item.type_id"
              :label="$t('type')"
            />
          </v-col>
          <v-col cols="3">
            <v-autocomplete
              :items="categories"
              item-text="name"
              item-value="id"
              v-model="item.categories"
              multiple
              :label="$t('category')"
            />
          </v-col>
          <v-col cols="3">
            <v-file-input
              v-model="cover_image"
              :label="$t('cover_image')"
              hide-details
            ></v-file-input>
            <v-img v-if="cover_image" :src="get_url(cover_image)" width="300">
            </v-img>
            <!-- <v-img :src="get_url(image)" width="300"></v-img> -->
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-text-field
              v-model="new_tag"
              append-icon="fas fa-plus"
              :label="$t('add_tag')"
              @click:append="tags.push({ name: new_tag })"
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <v-chip
              v-for="(tag, index) in tags"
              class="ma-2"
              close
              color="primary"
              text-color="white"
              close-icon="mdi-delete"
              @click:close="tags.splice(index, 1)"
            >
              {{ tag.name }}
            </v-chip>
          </v-col>
        </v-row>
        <v-row
          v-for="(translation, index) in item.translations.filter(
            (i) => i.field == 'description'
          )"
        >
          <v-col cols="2">
            <v-autocomplete
              dense
              v-model="translation.locale"
              :items="locales"
              item-text="locale.native_name"
              item-value="locale.iso_639"
            >
            </v-autocomplete>
          </v-col>
          <v-col cols="8">
            <ckeditor
              :editor="editor"
              v-model="translation.value"
              :config="editorConfig"
            ></ckeditor>
          </v-col>
          <v-col cols="2">
            <v-btn
              icon
              v-if="index > 0"
              @click="
                item.translations.splice(
                  item.translations.findIndex(
                    (i) =>
                      i.field == 'description' && i.locale == translation.locale
                  ),
                  1
                )
              "
            >
              <v-icon> fas fa-times </v-icon>
            </v-btn>
            <v-btn @click="item.translations.push({ field: 'content' })" icon>
              <v-icon> fas fa-plus </v-icon>
            </v-btn>
          </v-col> </v-row
        ><v-row>
          <v-col cols="12">
            <v-card>
              <v-card-title>
                {{ $t("item_images") }}
              </v-card-title>
              <v-card-text>
                <v-row>
                  <v-col cols="4" v-for="(image, index) in images">
                    <v-hover>
                      <template v-slot:default="{ hover }">
                        <v-img :src="get_url(image)" width="300">
                          <v-fade-transition>
                            <v-overlay v-if="hover" absolute color="#e4e4e4">
                              <v-btn
                                icon
                                dark
                                color="#222222"
                                size="64"
                                @click="images.splice(index, 1)"
                              >
                                <v-icon>mdi-minus</v-icon></v-btn
                              >
                            </v-overlay>
                          </v-fade-transition>
                        </v-img>
                      </template>
                    </v-hover>
                  </v-col>
                  <v-col cols="4">
                    <v-btn @click="$refs.inputUpload.click()" icon>
                      <v-icon> mdi-plus </v-icon>
                      <input
                        v-show="false"
                        ref="inputUpload"
                        type="file"
                        @change="handleFileUpload"
                        multiple
                      />
                      <!-- <v-img :src="get_url(image)" width="300"></v-img> -->
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-btn dark color="primary" block @click="save(item)">
              <v-icon> mdi-check </v-icon>
              {{ $t("save") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
    </base-material-card>
  </v-container>
</template>
<script>
import { mapActions, mapState } from "vuex";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import UploadAdapter from "../../../plugins/uploadAdapter";
function UploadAdapterPlugin(editor) {
  editor.plugins.get("FileRepository").createUploadAdapter = (loader) => {
    return new UploadAdapter(loader);
  };
}
export default {
  props: {
    item: {
      type: Object,
      translations: Array,
      categories:Array,
      default: () => {
        return {
          translations: [
            {
              locale: "ar",
              field: "name",
            },
            {
              locale: "ar",
              field: "description",
            },
          ],
          categories:[]
        };
      },
    },
  },
  data() {
    return {
      images: [],
      tags: [],
      cover_image: null,
      editor: ClassicEditor,
      editorConfig: {
        extraPlugins: [UploadAdapterPlugin],
      },
    };
  },
  mounted() {
    this.$store.dispatch("type/index");
    this.$store.dispatch("category/index");
    this.$store.dispatch("locales/index");
    this.$store.dispatch("item/index");
  },
  methods: {
    ...mapActions("item", ["store"]),
    get_url(image) {
      return URL.createObjectURL(image);
    },
    handleFileUpload(e) {
      console.log(e);
      let files = e.target.files;
      for (var i = 0; i < files.length; i++) {
        this.images.push(files[i]);
      }
      // for( let file in e.target.files)
      // this.images.concat(e.target.files);
    },
    async save(item) {
      let gallery = await this.$store.dispatch("gallery/store", {
        name: this.item.name,
        cover_image: this.cover_image,
        is_file: true,
      });
      if (this.images.length > 0) {
        this.images.map((image) => {
          image = {
            file: image,
            gallery_id: gallery.id,
            is_file: true,
          };

          this.$store.dispatch("media/store", image);
        });
      }
      item.gallery_id = gallery.id;
      console.log(item);
      let item_data = await this.$store.dispatch("item/store", item);

      if (this.tags.length > 0) {
        let tags_data = {
          item_id: item_data.id,
          tags: this.tags,
        };
        this.$store.dispatch("item_tags/store", tags_data);
      }
    },
  },
  computed: {
    ...mapState({
      types: (state) => state.type.all,
      categories: (state) => state.category.all,
      locales: (state) => state.locales.all,
      items: (state) => state.item.all,
    }),
  },
};
</script>