<template>
  <v-container id="regular-tables" fluid tag="section">
    <base-v-component heading="items_form" />

    <base-material-card
      icon="mdi-clipboard-text"
      title="Item Form"
      class="px-5 py-3"
    >
      <v-form>
        <v-row>

          <v-col cols="3">
            <v-autocomplete
              dense
              v-model="item.item_id"
              :items="items"
              item-text="name"
              item-value="id"
              :label="$t('item')"
            >
            </v-autocomplete>
          </v-col>
          <v-col cols="3">
            <v-autocomplete
              dense
              v-model="item.accessor_item_id"
              :items="items"
              item-text="name"
              item-value="id"
              :label="$t('accessor')"
            >
            </v-autocomplete>
          </v-col>
          <v-col cols="3">
            <v-text-field
              v-model="item.qty"
              :label="$t('quantity')"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-btn dark color="primary" block @click="store(item)">
              <v-icon> mdi-check </v-icon>
              {{ $t("save") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
    </base-material-card>
  </v-container>
</template>
<script>
import { mapActions, mapState } from "vuex";
export default {
  props: {
    item: {
      default: () => {
        return {
        };
      },
    },
  },
  mounted() {
    this.$store.dispatch("item/index");
  },
  methods: {
    ...mapActions("accessor", ["store"]),
  },
  computed: {
    ...mapState({
      items:(state)=>state.item.all
    }),
  },
};
</script>